@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap');

body {
  margin: 0;
  font-family: 'Roboto', sans-serif;
}

header {
  padding: 83px 50px 19px 50px;
}

header ul {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 30px;
  list-style-type: none;
  padding-left: 0px;
}

header ul li a {
  text-decoration: none;
  color: #ffffff;
  font-size: 20px;
  text-transform: uppercase;
}

header ul li {
  width: 225px;
  height: 56px;
  background: rgb(15 118 110/1);
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
}

.container {
  width: 100%;
  max-width: 950px;
  margin: auto;
}

.CityName h2 {
  font-size: 33px;
  color: #286279;
  margin: 0 0 20px;
  text-align: center;
}

.CityName textarea {
  border: 1px solid #ddd;
  width: 100%;
  padding: 15px;
  margin-bottom: 10px;
  outline: none;
}

.button {
  padding: 10px 25px;
  background-color: rgb(15 118 110/1);
  color: #ffffff;
  font-size: 16px;
  border: none;
  cursor: pointer;
}

.SongList h2 {
  font-size: 33px;
  color: #286279;
  margin: 0 0 20px;
  text-align: center;
}

.SongListInput {
  display: flex;
  gap: 10px;
}

select {
  border: 1px solid #ddd;
  padding: 10px;
  width: 100%;
  outline: 0;
}

.SongList p,.CityName p  {
  border: 1px solid #ddd;
  padding: 20px;
  background-color: #eee;
}

